body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.next-steps .fa-link {
  margin-right: 5px;
}

/* Fix for use only flexbox in content area */
.next-steps .row {
  margin-bottom: 0;
}

.next-steps .col-md-5 {
  margin-bottom: 3rem;
}

@media (max-width: 768px) {
  .next-steps .col-md-5 {
    margin-bottom: 0;
  }
}

.spinner {
  position: absolute;
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.result-block-container .result-block {
  opacity: 1;
}

.loading {
  display: flex;
  min-height: 500px;
  align-items: center;
  justify-content: center;
}

.userImg {
  border-radius: 100px;
  display: block;
  height: 100px;
  margin: 0 auto;
  width: 100px;
}

.hero .app-logo {
  max-width: 10.5rem;
}

.logo {
  background-image: url("/src/assets/logo.png");
    width: 4rem;
    height: 4rem;
    background-repeat: no-repeat;
    background-size: cover;
}

.hero-arrow {
  width: auto;
  height: 3rem;
  margin-top: 4rem;
}

.navbar {
  padding: 1em;
}

.login-btn {
  background-color: black;
  font-size: 1.2rem;
  font-weight: bold;
  /* border-color: #0166F9; */
  color: white;
}

.logout-btn {
  background-color: white;
  font-size: 1.2rem;
  font-weight: bold;
  /* border-color: #0166F9; */
  color: red;
}

.dj-icon {
  width: 10rem;
  margin: 0 auto;
  padding: 1em;
}

.border-1 {
  border-color: #0052D4;
}
.gradient-bg {
background: #0052D4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #6FB1FC, #4364F7, #0052D4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #6FB1FC, #4364F7, #0052D4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.text-x2 {
  font-size: 1.25rem;
}

.info-icon {
  width: 65%;
  margin: 0 auto;
  display: block;
}

.rated-dj-icon {
  width: 25%;
  margin: 0 auto;
  display: block;
  margin-top: 1em;
}

.equipment-icon {
  width: 25%;
  margin: 0 auto;
  display: block;
  margin-top: 2em;
}

.tr-dj-card {
  border-radius: 10%;
}

.profile-btn {
  color: #fff;
  background: #851DFF;
}

.section-2 {
  background: #f8f9fa;
  border-radius: 1rem;
}

.section-4 {
  border-radius: 1rem;
}

/* Bootstrap 4 text input with search icon */

.has-search .form-control {
  padding-left: 2.375rem;
}

.has-search .form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #aaa;
}

.profile-list {
  margin-left: 20vw;
}